export const metadata = {
  title: "Blog | Cloud101",
  description: "Explore expert articles, tutorials, and insights on Salesforce, AI, cloud technologies, and career growth."
};

export default function BlogHomePage() {
  // Dummy blog data (Replace with actual data or fetch API)
  const blogs = [
    {
      id: 1,
      title: "Getting Started with Salesforce Data Cloud",
      excerpt: "A beginner-friendly introduction to Data Cloud and how it transforms customer 360 experiences.",
      category: "Salesforce",
      date: "December 2025",
      readTime: "5 min read"
    },
    {
      id: 2,
      title: "Agentforce: Building AI Agents in Salesforce",
      excerpt: "Learn how to configure and deploy intelligent AI agents inside Salesforce using Agentforce.",
      category: "AI & Automation",
      date: "December 2025",
      readTime: "7 min read"
    },
    {
      id: 3,
      title: "LWC Best Practices for High-Performance Apps",
      excerpt: "Optimize your Lightning Web Components with performance, patterns, and real-world examples.",
      category: "Development",
      date: "November 2025",
      readTime: "6 min read"
    }
  ];

  return (
    <main className="max-w-6xl mx-auto px-5 md:px-0 py-12">
      
      {/* Hero Section */}
      <section className="text-center mb-12">
        <h1 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
          Cloud101 Blog
        </h1>
        <p className="text-lg text-gray-600 max-w-2xl mx-auto">
          Insights, tutorials, and expert knowledge on Salesforce, AI, development, and career growth.
        </p>
      </section>

      {/* Blog Grid */}
      <section>
        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {blogs.map((blog) => (
            <a
              key={blog.id}
              href={`/blog/${blog.id}`}
              className="block border border-gray-200 rounded-xl p-6 shadow-sm hover:shadow-md transition duration-200 hover:-translate-y-1 bg-white"
            >
              <span className="inline-block bg-blue-100 text-blue-700 text-xs font-medium px-3 py-1 rounded-full mb-3">
                {blog.category}
              </span>
              
              <h2 className="text-xl font-semibold text-gray-900 mb-2">
                {blog.title}
              </h2>

              <p className="text-gray-600 mb-4">
                {blog.excerpt}
              </p>

              <div className="text-sm text-gray-500 flex justify-between items-center">
                <span>{blog.date}</span>
                <span>{blog.readTime}</span>
              </div>
            </a>
          ))}
        </div>
      </section>

      {/* Footer CTA */}
      <section className="mt-16 text-center">
        <p className="text-gray-600 mb-4">Want to stay updated with new articles?</p>
        <a 
          href="/subscribe"
          className="px-6 py-3 bg-blue-600 text-white rounded-lg text-sm font-medium hover:bg-blue-700 transition"
        >
          Subscribe Now
        </a>
      </section>
    </main>
  );
}
